import React from 'react';

const Tablists = props => {
    const {children} = props;
    return (
        <ul>
            {children.map(list => {
                const {label} = list.props;
                
                return (
                    <li key={label}>{label}</li>
                )
            })}
        </ul>
    )
}

export default Tablists;